<template>
	<div class="contentWrapper">
		<h1>Discovering Who Your Followers Follow</h1>
		<search-bar v-on:start-search="sendQuery" :disabled="loadingData" ref="searchBar"></search-bar>
		<div class="wraps" id="view-wrapper" :if="view !== 'empty'">
			<transition name="compenent-fade" mode="out-in">
				<component :is="view" :list="followerList" :error="errorMessage" v-on:tile-selected="processTileClick" ></component>
			</transition>
		</div>
	</div>
</template>

<script>
import LoadingSpinner from '../Loading'
import SearchBar from './SearchBar'
import RankedList from './RankedList'
import Error from './Error'
import Empty from './Empty'

export default {
	name: 'mapper',
	data () {
		return {
			followerList: null,
			errorMessage: null,
			view: 'empty',
			viewSizeOptions: {
				rankedList: { height: '500px', width: '100%' },
				spinner: { height: '100px', width: '100px' },
				error: { height: '150px', width: '100%' },
				empty: { height: '0px', width: '0px' }
			}
		}
	},
	components:{
		SearchBar,
		'rankedList': RankedList,
		'spinner': LoadingSpinner,
		'error': Error,
		'empty': Empty
	},
	computed:{
		loadingData: function(){
			return this.view === 'spinner';
		}
	},
	methods: {
		sendQuery: function (options){
			if (options){
				this.changeWrapsDimensions('spinner');
				this.errorMessage = null;
				this.followerList = null;
				var env = 'http://www.twitchmapper.us-west-2.elasticbeanstalk.com/api/';
				if(location.host.indexOf('localhost:8080') >= 0){
					env = 'http://localhost:3000/api/';
				}
				this.$http.get(env + options.searchType + '/' + options.targetStream)
				.then(
					//success
					(response) => {
						if(response.body.error){
							//show error message
							this.changeWrapsDimensions('error');
							this.errorMessage = response.body
						}
						else{
							this.changeWrapsDimensions('rankedList');
							this.followerList = response.body;						}
					},
					//error
					(response) => {
						this.changeWrapsDimensions('error');
						this.errorMessage = {message: "We're not sure what happened but it sure wasn't good"}
						console.log(response);
					}
				);
			}
		},
		checkQueryString: function(){
			this.$nextTick(function(){
				if(location.hash.indexOf('access_token') >= 0){
					var options = {'searchType': this.getVariableQueryString('state'), 'targetStream': this.getVariableQueryString('access_token')}
					this.sendQuery(options);
				}
			})
		},
		getVariableQueryString : function(variable){
			var svalue = location.hash.match(new RegExp("[\?\&\?\#]" + variable + "=([^\&]*)(\&?)","i"));
			return svalue ? svalue[1] : svalue;
		},
		changeWrapsDimensions: function(state){
			this.view = state;
			var el = document.getElementById("view-wrapper");
			el.style.height = getComputedStyle(el).height;
			el.style.width = getComputedStyle(el).width;
			el.offsetHeight;
			el.style.height = this.viewSizeOptions[this.view].height;
			el.style.width = this.viewSizeOptions[this.view].width;
		},
		processTileClick: function(options){
			this.$refs.searchBar.targetStream = options;
		}

	},
	mounted() { 
		this.changeWrapsDimensions('empty');
		this.checkQueryString()
	}
}
</script>

<style scoped>
	#view-wrapper {
		position: relative;
		margin: auto;
		transition: height 0.5s ease-in-out, width 0.5s ease-in-out;
	}
	.compenent-fade-enter-active {
		transition: opacity 1s ease;
	}
	.compenent-fade-leave-active{
		transition: opacity 0.15s ease;
	}
	.compenent-fade-enter, .compenent-fade-leave-active {
		opacity: 0;
	}
	p{
		font-size: 10px;
	}

	@media screen 
		and (max-width: 40.000em){ /*960px*/
		#view-wrapper { 
			max-height: 250px;
		}
	}
</style>


