<template>
	<div class="stream-list-wrap">
		<stream-tile v-for="stream in list" :stream='stream' :selected='selected' v-on:tile-selected="selectTile"></stream-tile>
	</div>
</template>

<script>
import StreamTile from './StreamTile'

export default {
	name: 'ranked-list',
	props: ['list'],
	data () {
	return {
			selected: this.list[0].channel
		}
	},
	components:{
		StreamTile
	},
	methods: {
		selectTile: function(target){
			this.selected = target;
			this.$emit('tile-selected', target);

		}
	}
}

</script>

<style scoped>
.stream-list-wrap{
	height: 500px;
	margin-top: 10px;
	overflow-y: scroll;
}
@media screen 
	and (max-width: 40.000em){ /*960px*/
	.stream-list-wrap { 
		height: 250px;
	}
}
</style>


