<template>
	<div>
		<ul class="search-wrapper">
			<li class="left">
				<div class="radio-button" v-on:click="clearField('followers')">		
					<input type="radio" id="followers" name="followers" value="followers" v-model="searchType" v-bind:disabled="disabled" tabindex="1"/>
					<label for="followers">followers</label>
				</div>
				<div class="radio-button" v-on:click="clearField('subs')" >
					<input type="radio" id="subs" name="subs" value="subs" v-model="searchType" v-bind:disabled="disabled" tabindex="3"/>
					<label for="subs">subscribers*</label>
				</div>
			</li>
			<li class="right">
				<transition name="compenent-fade" mode="out-in">
					<input v-if="searchType != 'subs'" v-model="targetStream" v-bind:disabled="disabled" placeholder="Channel Name" type="text" name="targetStream" class='target-stream'  v-bind:class="{ 'highlight': highlighted }"id="targetStream" @keyup.enter="sendQuery()" tabindex="2"/>
				</transition>
				<label class="hide" for="targetStream">Target Channel</label>
				<button v-bind:disabled="disabled" v-on:click="sendQuery()" tabindex="4">Do The Thing</button>
			</li>
		</ul>
		<span>*Twitch authorization required</span>
	</div>
</template>

<script>
export default {
	name: 'search-bar',
	data () {
	return {
			targetStream: '',
			highlighted: false,
			searchType: 'followers'
		}
	},
	props: ['disabled'],
	methods: {
		sendQuery : function(){

			this.highlighted = false;
			if(this.searchType === 'followers' && this.targetStream === ''){
				this.highlighted = true;
				return;
			}
			if(!this.disabled){
				if(this.searchType === 'subs'){
					this.getTwitchAuth();
				}
				this.$emit('start-search', {'targetStream': this.targetStream, 'searchType': this.searchType});
			}
		},
		getTwitchAuth : function(){
			if(!(location.hash.indexOf('access_token') >= 0)){
				var url = [location.protocol, '//', location.host, location.pathname].join('');
				var client_id = 'icpumhuecx97l8d1c709g8d0j7otfw';
				if(location.host.indexOf('localhost:8080') >= 0){
					client_id = 'wu8coxnihxvjotyn17laqxqb6c9ec3';
				}
				window.location = "https://api.twitch.tv/kraken/oauth2/authorize?response_type=token&client_id="+client_id+"&redirect_uri="+url+"&scope=channel_subscriptions channel_read&state="+ this.searchType;
			}
			else{
				this.targetStream = this.getVariableQueryString('access_token');
			}
		},
		getVariableQueryString : function(variable){
			var svalue = location.hash.match(new RegExp("[\?\&\?\#]" + variable + "=([^\&]*)(\&?)","i"));
			return svalue ? svalue[1] : svalue;
		},
		clearField : function(target){
			if(target !== this.searchType){
				this.targetStream = "";
			}
		},
		onLoad: function(){
			var test = this.getVariableQueryString('state');
			if(test !== null){
				this.searchType = test;
			}
		}
	},
	mounted() {
		this.onLoad()
	}
}

</script>

<style scoped>
	.radio-button{	
		margin: 5px;
	}
	.radio-button label {
		vertical-align: top;
		margin-right: 3%;
	}
	.radio-button input {
		display: inline-block;
		vertical-align: top;
	}
	.search-wrapper{
		list-style-type: none;   
		padding: 0;
    	margin-bottom: 2px;
	}
	.search-wrapper li {
		margin: 0 10px;
		display: inline-block;
	}
	.target-stream{
		text-align: center;
		display: block;
		width: 125px;
		margin: 2px;
	}
	.left{
		text-align: left;
		min-width: 125px;
	}
	.right{
		text-align: right;
		min-width: 125px;
	}
	.compenent-fade-enter-active {
		transition: opacity .5s ease;
	}
	.compenent-fade-leave-active{
		transition: opacity .5s ease;
	}
	.compenent-fade-enter, .compenent-fade-leave-active {
		opacity: 0;
	}
	button{
		padding: 2px 10px;
		margin: 2px;
		width: 130px;
	}
	span{
		font-size: 10px;
	}
	.hide {
		position: absolute !important;
		top: -9999px !important;
		left: -9999px !important;
	}
	.highlight, .target-stream:focus{
		border: 2px solid #dadada;
		border-color: #9ecaed;
		box-shadow: 0 0 10px #9ecaed;
		outline: none;
	}
</style>


