<template>
  <div class="description">
    <h3>Overview</h3>
    <p>Just enter a channel name into the input field and do the thing! The tool takes a representative sampling (~300) of that channel's most recent followers and returns the most commonly followed channels among them. If you're a twitch partner you can choose to sample from your subscribers instead, but that requires authorization through twitch. The number in parenthesis is the total number of your sampled followers that follow that particular channel. The hope is that this app can provide some insight into a stream's often opaque twitch community.</p>
    <h3>Interpreting the Results</h3>
    <p>The streams with a large numbers of followers tend to return other larger streams, while smaller streams typically reveal the niche communities they belong to. If you tend to favor a specific type of game you will probably see other streamers who also favor the game/genre. Either way it gives you a good idea of who your viewers also watch and more information is always useful. Also Bob Ross is remarkably pervasive.</p>
    <h3>Sampling Process</h3>
    <p>The app first takes a representative sampling, about 300, of the most recent followers/subs on a channel, then it looks at the last 100 channels those followers have followed and aggregates a count of the common channels between them all. It orders the final list, trims the results to the top 60, bundles it together and returns it to the browser. It's important to note that the sampling is only representative of a channels last 1600 followers as that's the maximum offset you can query a channel's followers for through the twitch API.</p>
  </div>
</template>

<script>
export default {
  name: 'description'
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
  text-align: center;
}

.description{
  margin:30px;
  background: white;
  padding: 5px 50px;
  text-align: left;
  min-width: 300px;
}
@media screen 
  and (max-width: 60.000em){ /*960px*/
    .description{
      margin:30px 10px;
      padding: 1px 15px;
    }
}
</style>



