<template>
	<div class="error">
		<h2>There was a problem :(</h2>
		<span>{{error.message}}</span>
	</div>
</template>

<script>
export default {
	name: 'error',
	props: ['error']
}
</script>

<style scoped>
	.error{
		width: 350px;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		border: 1px solid black;
		background: #fff;
	}
	span, h2{
		display: block;
		margin: 10px;
	}
</style>


