<template>
	<div class="stream-tile" v-bind:class="{selected: isSelected}" v-on:click="selectTile(stream.channel)" tabindex="0" @keyup.enter="selectTile(stream.channel)" v-bind:aria-label="'Result: ' + stream.channel">
		<div class="stream-info">
			<span><a v-bind:href="'https://www.twitch.tv/' + stream.channel" target="_blank" title="Go to channel"><img v-bind:src="logo" :alt="stream.channel + ' logo'"></a></span>
			<span>{{stream.channel}} ({{stream.count}})</span>
			<span>followers: {{stream.followers.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}}</span>
			<span>{{stream.partner ? "*Partner*" : "&nbsp;"}}</span>
		</div>
	</div>
</template>

<script>
export default {
	name: 'stream-tile',
	props: ['stream', 'selected'],
	computed:{
		isSelected: function(){
			return this.selected === this.stream.channel;
		},
		logo: function(){
			return this.stream.logo === null ? 'https://static-cdn.jtvnw.net/jtv_user_pictures/xarth/404_user_70x70.png' : this.stream.logo
		}
	},
	methods: {
		selectTile : function(target){
			this.$emit('tile-selected', target);
		}
	}
}
</script>

<style scoped>
.stream-tile{
	display: inline-block;
	margin: 10px;
	width: 160px;
	height: 175px;
	background: #fff;
	transition: background-color 0.25s ease-in-out, color 0.25s ease-in-out;
	cursor: pointer;
}
.stream-info{
	font-size: 14px;
}
.stream-tile:hover{
	box-shadow: 0px 0px 20px 0px #846bb3;
	background: #836ab2;
	color: #fff;
}
.selected, .selected:hover{
	box-shadow: 0px 0px 20px 0px #846bb3;
	background: #6441A4;
	color: #fff;
}
img{
	width: 75px;
	height: 75px;
	border-radius: 50%;
}
img:hover{
	box-shadow: 0px 0px 15px 0px #fff; 
}
span{
	white-space: pre;
	display: block;
	margin: 10px 0 ;
}
</style>


